body {
    font-family: Arial, sans-serif;
    background-color: #f5f5f5;
    margin: 0;
    padding: 0;
}

h2 {
    color: #b22222; /* Dark Red */
    text-align: center;
}

.yeni-hakedis-button {
    background-color: #b22222; /* Dark Red */
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    margin: 20px;
    cursor: pointer;
    border-radius: 4px;
}

.yeni-hakedis-button:hover {
    background-color: #800000; /* Darker Red */
}

.hakediş-listesi-tablosu {
    width: 97.5%;
    margin: 0 auto;
    border-collapse: collapse;
}

.hakediş-listesi-tablosu th, .hakediş-listesi-tablosu td {
    border: 1px solid #ddd;
    padding: 8px;
}

.hakediş-listesi-tablosu th {
    background-color: #b22222; /* Dark Red */
    color: white;
    text-align: left;
}

.hakediş-listesi-tablosu tr:nth-child(even) {
    background-color: #f2f2f2;
}

.hakediş-listesi-tablosu tr:hover {
    background-color: #ddd;
}

.düzenle-button, .sil-button {
    background-color: #b22222; /* Dark Red */
    color: white;
    border: none;
    padding: 5px 10px;
    margin: 2px;
    cursor: pointer;
    border-radius: 4px;
}

.düzenle-button:hover, .sil-button:hover {
    background-color: #800000; /* Darker Red */
}
